@tailwind base;
@tailwind components;
@tailwind utilities;

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  margin: 0;
  /* font-family: 'Inter Variable', 'SF Pro Display', -apple-system, BlinkMacSystemFont, 'Segoe UI Variable', 'Roboto Flex', sans-serif; */
  font-family: 'Inter Variable', 'SF Pro Display', -apple-system, BlinkMacSystemFont, 'Segoe UI Variable', system-ui, sans-serif;

  background: hsl(var(--background));
  color: hsl(var(--foreground));
  transition: all 0.2s cubic-bezier(0.4, 0, 0.2, 1);
  line-height: 1.6;
  font-feature-settings: 'rlig' 1, 'calt' 1, 'ss01' 1, 'ss02' 1;
  text-rendering: optimizeLegibility;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-optical-sizing: auto;
  font-variation-settings: 'wght' 400;
}

@layer base {
  :root {
    --background: 0 0% 100%; /* white */
    --foreground: 0 0% 2%;   /* ultra modern dark */
    --card: 0 0% 100%;
    --card-foreground: 0 0% 2%;
    --popover: 0 0% 100%;
    --popover-foreground: 0 0% 2%;
    --primary: 0 0% 6%;
    --primary-foreground: 0 0% 98%;
    --secondary: 0 0% 97.5%;
    --secondary-foreground: 0 0% 6%;
    --muted: 0 0% 97.5%;
    --muted-foreground: 0 0% 46%;
    --accent: 0 0% 97.5%;
    --accent-foreground: 0 0% 6%;
    --destructive: 0 0% 6%;
    --destructive-foreground: 0 0% 98%;
    --border: 0 0% 92%;
    --input: 0 0% 92%;
    --ring: 0 0% 2%;
    --radius: 1rem;
    /* Modern shadows with depth */
    --shadow-xs: 0 1px 2px 0 rgba(0, 0, 0, 0.05);
    --shadow-sm: 0 1px 3px 0 rgba(0, 0, 0, 0.1), 0 1px 2px -1px rgba(0, 0, 0, 0.1);
    --shadow-md: 0 4px 6px -1px rgba(0, 0, 0, 0.1), 0 2px 4px -2px rgba(0, 0, 0, 0.1);
    --shadow-lg: 0 10px 15px -3px rgba(0, 0, 0, 0.1), 0 4px 6px -4px rgba(0, 0, 0, 0.1);
    --shadow-xl: 0 20px 25px -5px rgba(0, 0, 0, 0.1), 0 8px 10px -6px rgba(0, 0, 0, 0.1);
    --shadow-2xl: 0 25px 50px -12px rgba(0, 0, 0, 0.15);
    /* Modern gradients */
    --gradient-subtle: linear-gradient(135deg, hsl(0 0% 100%) 0%, hsl(0 0% 99%) 100%);
    --gradient-depth: linear-gradient(135deg, hsl(0 0% 100%) 0%, hsl(0 0% 97%) 50%, hsl(0 0% 95%) 100%);
  }  .dark {
    --background: 0 0% 2%; /* ultra modern dark */
    --foreground: 0 0% 98%; /* crisp white */
    --card: 0 0% 3%;
    --card-foreground: 0 0% 98%;
    --popover: 0 0% 3%;
    --popover-foreground: 0 0% 98%;
    --primary: 0 0% 98%;
    --primary-foreground: 0 0% 6%;
    --secondary: 0 0% 12%;
    --secondary-foreground: 0 0% 98%;
    --muted: 0 0% 12%;
    --muted-foreground: 0 0% 65%;
    --accent: 0 0% 12%;
    --accent-foreground: 0 0% 98%;
    --destructive: 0 0% 98%;
    --destructive-foreground: 0 0% 6%;
    --border: 0 0% 12%;
    --input: 0 0% 12%;
    --ring: 0 0% 85%;
    /* Enhanced dark shadows */
    --shadow-xs: 0 1px 2px 0 rgba(0, 0, 0, 0.2);
    --shadow-sm: 0 1px 3px 0 rgba(0, 0, 0, 0.3), 0 1px 2px -1px rgba(0, 0, 0, 0.3);
    --shadow-md: 0 4px 6px -1px rgba(0, 0, 0, 0.4), 0 2px 4px -2px rgba(0, 0, 0, 0.4);
    --shadow-lg: 0 10px 15px -3px rgba(0, 0, 0, 0.5), 0 4px 6px -4px rgba(0, 0, 0, 0.5);
    --shadow-xl: 0 20px 25px -5px rgba(0, 0, 0, 0.6), 0 8px 10px -6px rgba(0, 0, 0, 0.6);
    --shadow-2xl: 0 25px 50px -12px rgba(0, 0, 0, 0.7);
    /* Dark mode gradients */
    --gradient-subtle: linear-gradient(135deg, hsl(0 0% 2%) 0%, hsl(0 0% 4%) 100%);
    --gradient-depth: linear-gradient(135deg, hsl(0 0% 2%) 0%, hsl(0 0% 6%) 50%, hsl(0 0% 10%) 100%);
  }
}

/* Legacy support for data-theme */
[data-theme="dark"] {
  color-scheme: dark;
}

/* Ultra-modern effects and interactions */
.dark {
  /* Enhanced shadows with multiple layers */
  --tw-shadow-color: 0 0% 0%;
  --tw-shadow-colored: var(--shadow-2xl);
}

/* Next-gen card hover effects */
.modern-card {
  position: relative;
  overflow: hidden;
  transition: all 0.4s cubic-bezier(0.25, 0.46, 0.45, 0.94);
}

.modern-card::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.05), transparent);
  transition: left 0.6s ease;
}

.modern-card:hover::before {
  left: 100%;
}

.modern-card:hover {
  transform: translateY(-8px) scale(1.02);
  box-shadow: var(--shadow-2xl);
}

/* Glassmorphism 2.0 */
.glass-modern {
  backdrop-filter: blur(24px) saturate(180%);
  -webkit-backdrop-filter: blur(24px) saturate(180%);
  background: hsla(var(--background), 0.8);
  border: 1px solid hsla(var(--border), 0.2);
  box-shadow: 
    0 8px 32px 0 rgba(31, 38, 135, 0.15),
    inset 0 1px 0 0 rgba(255, 255, 255, 0.1);
}

/* Modern focus states with enhanced visibility */
:focus-visible {
  outline: 2px solid hsl(var(--ring));
  outline-offset: 4px;
  border-radius: var(--radius);
}

/* Enhanced scroll behavior */
html {
  scroll-behavior: smooth;
  scroll-padding-top: 6rem;
  overflow-x: hidden;
}

/* Ultra-smooth animations with advanced easing */
* {
  transition-property: color, background-color, border-color, text-decoration-color, fill, stroke, opacity, box-shadow, transform, filter, backdrop-filter, scale, rotate, translate;
  transition-timing-function: cubic-bezier(0.25, 0.46, 0.45, 0.94);
  transition-duration: 200ms;
}

/* Modern button states */
.btn-modern {
  position: relative;
  overflow: hidden;
  transform-style: preserve-3d;
}

.btn-modern::after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(45deg, transparent 30%, rgba(255, 255, 255, 0.1) 50%, transparent 70%);
  transform: translateX(-100%);
  transition: transform 0.5s ease;
}

.btn-modern:hover::after {
  transform: translateX(100%);
}

/* Text shimmer effect */
.text-shimmer {
  background: linear-gradient(
    90deg,
    hsl(var(--foreground)) 25%,
    hsl(var(--muted-foreground)) 50%,
    hsl(var(--foreground)) 75%
  );
  background-size: 200% auto;
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  animation: shimmer 3s ease-in-out infinite;
}

@keyframes shimmer {
  0% { background-position: 200% center; }
  100% { background-position: -200% center; }
}

/* Enhanced selection with modern colors */
::selection {
  background: hsla(var(--foreground), 0.12);
  color: hsl(var(--foreground));
}

/* Modern loading states */
.skeleton {
  background: linear-gradient(
    90deg,
    hsl(var(--muted)) 25%,
    hsl(var(--accent)) 50%,
    hsl(var(--muted)) 75%
  );
  background-size: 200% 100%;
  animation: loading 1.5s infinite;
}

@keyframes loading {
  0% { background-position: 200% 0; }
  100% { background-position: -200% 0; }
}

/* Ultra-modern Responsive Design */
@media (max-width: 768px) {
  .desktop-nav {
    display: none !important;
  }
  
  .mobile-nav {
    display: flex !important;
  }

  .navbar-brand {
    font-size: 1.375rem !important;
    font-weight: 700 !important;
    font-variation-settings: 'wght' 700;
  }

  .navbar {
    padding: 1.25rem 1.5rem !important;
    backdrop-filter: blur(20px) saturate(180%);
    -webkit-backdrop-filter: blur(20px) saturate(180%);
    background: hsla(var(--background), 0.85);
    border-bottom: 1px solid hsla(var(--border), 0.3);
  }

  /* Ultra-modern mobile menu */
  .mobile-menu {
    background: var(--gradient-subtle);
    backdrop-filter: blur(24px);
    border-radius: 1.5rem;
    margin: 1rem;
    padding: 1rem;
    border: 1px solid hsla(var(--border), 0.2);
    box-shadow: var(--shadow-xl);
  }

  .mobile-menu-item {
    border-bottom: 1px solid hsla(var(--border), 0.3);
    padding: 1.25rem 1rem;
    border-radius: 0.75rem;
    transition: all 0.3s cubic-bezier(0.25, 0.46, 0.45, 0.94);
    position: relative;
  }

  .mobile-menu-item:last-child {
    border-bottom: none;
  }

  .mobile-menu-item:hover {
    background: hsla(var(--accent), 0.6);
    transform: translateX(8px) scale(1.02);
    box-shadow: var(--shadow-md);
  }

  /* Enhanced touch targets */
  button, a, .interactive {
    min-height: 48px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 0.75rem;
    transition: all 0.3s cubic-bezier(0.25, 0.46, 0.45, 0.94);
  }

  /* Modern container spacing */
  .container {
    padding: 0 1.5rem;
    max-width: 100%;
  }

  /* Enhanced card spacing for mobile */
  .card-mobile {
    margin: 1.5rem 0;
    padding: 2rem 1.5rem;
    border-radius: 1.5rem;
  }
}

@media (min-width: 769px) {
  .mobile-nav {
    display: none !important;
  }
  
  .desktop-nav {
    display: flex !important;
  }

  /* Ultra-modern hover states for desktop */
  .hover-lift {
    transition: all 0.4s cubic-bezier(0.25, 0.46, 0.45, 0.94);
  }

  .hover-lift:hover {
    transform: translateY(-6px) scale(1.02);
    box-shadow: var(--shadow-xl);
  }

  .hover-scale:hover {
    transform: scale(1.05);
    transition: transform 0.3s cubic-bezier(0.34, 1.56, 0.64, 1);
  }

  /* Parallax-like effects */
  .parallax-card:hover {
    transform: perspective(1000px) rotateX(2deg) rotateY(2deg) translateZ(20px);
  }

  /* Modern link hover effects */
  .modern-link {
    position: relative;
    overflow: hidden;
  }

  .modern-link::after {
    content: '';
    position: absolute;
    bottom: 0;
    left: 0;
    width: 0;
    height: 2px;
    background: hsl(var(--foreground));
    transition: width 0.3s ease;
  }

  .modern-link:hover::after {
    width: 100%;
  }
}

/* Ultra-modern typography system */
.text-fluid-xs { font-size: clamp(0.75rem, 0.5vw + 0.65rem, 0.875rem); }
.text-fluid-sm { font-size: clamp(0.875rem, 0.5vw + 0.775rem, 1rem); }
.text-fluid-base { font-size: clamp(1rem, 0.5vw + 0.9rem, 1.125rem); }
.text-fluid-lg { font-size: clamp(1.125rem, 1vw + 1rem, 1.375rem); }
.text-fluid-xl { font-size: clamp(1.25rem, 1.5vw + 1.1rem, 1.75rem); }
.text-fluid-2xl { font-size: clamp(1.5rem, 2vw + 1.2rem, 2.25rem); }
.text-fluid-3xl { font-size: clamp(1.875rem, 3vw + 1.3rem, 3rem); }
.text-fluid-4xl { font-size: clamp(2.25rem, 4vw + 1.5rem, 4rem); }

/* Enhanced typography with modern features */
h1, h2, h3, h4, h5, h6 {
  letter-spacing: -0.05em;
  font-weight: 700;
  font-variation-settings: 'wght' 700;
  line-height: 1.2;
  text-wrap: balance;
}

/* Ultra-modern button system */
button, .btn {
  font-weight: 600;
  letter-spacing: -0.025em;
  font-variation-settings: 'wght' 600;
  border-radius: 0.75rem;
  transition: all 0.3s cubic-bezier(0.25, 0.46, 0.45, 0.94);
}

/* Next-gen link styles */
a {
  text-decoration-thickness: 1.5px;
  text-underline-offset: 3px;
  transition: all 0.2s ease;
}

a:hover {
  text-decoration-thickness: 2px;
  text-underline-offset: 4px;
}

/* Premium scrollbar design */
::-webkit-scrollbar {
  width: 12px;
  height: 12px;
}

::-webkit-scrollbar-track {
  background: hsl(var(--background));
  border-radius: 6px;
}

::-webkit-scrollbar-thumb {
  background: linear-gradient(180deg, hsl(var(--muted)), hsl(var(--muted-foreground)));
  border-radius: 6px;
  border: 2px solid hsl(var(--background));
}

::-webkit-scrollbar-thumb:hover {
  background: linear-gradient(180deg, hsl(var(--muted-foreground)), hsl(var(--foreground)));
}

/* Modern spacing utilities */
.space-ultra-tight { gap: 0.25rem; }
.space-tight { gap: 0.5rem; }
.space-normal { gap: 1rem; }
.space-loose { gap: 1.5rem; }
.space-ultra-loose { gap: 2.5rem; }

/* Enhanced contrast modes */
@media (prefers-contrast: high) {
  :root {
    --foreground: 0 0% 0%;
    --background: 0 0% 100%;
    --border: 0 0% 70%;
  }
  
  .dark {
    --foreground: 0 0% 100%;
    --background: 0 0% 0%;
    --border: 0 0% 30%;
  }
}

/* Reduced motion support */
@media (prefers-reduced-motion: reduce) {
  * {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
    scroll-behavior: auto !important;
  }
}
